.navbar {
    background-color: #0050F8;
    height: 70px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    box-shadow:rgba(0, 0, 0, 0.2) 0px 4px 8px;
}

.coast-companion {
    margin-left: 25px;
    font-size: x-large;
    font-weight: 700;
}

.navbar ul {
    padding: 0;
    margin-right: 25px;
    list-style: none;
    display: flex;
    align-items: center;
    gap: 2rem;
}

.navbar a {
    color: inherit;
    text-decoration: none;
}

.logout {
    background-color: white;
    text-align: center;
    color: #0050F8;
    height: 25px;
    width: 80px;
    font-size: small;
    font-weight: 500;
    border-radius: 20px;
    padding-top: 4px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px;
}

.logout:hover {
    background-color: #c3d6ff;
    color:#0050F8;
}

.dropdown-button {
    background-color: #0050F8;
    color: white;
    padding: 16px;
    font-size: medium;
    border: none;
  }
  
.dropdown {
    position: relative;
    display: inline-block;
}
  
.dropdown-content {
    display: none;
    position: absolute;
    background-color: white;
    min-width: 160px;
    box-shadow:rgba(0, 0, 0, 0.3) 0px 1px 3px;
    z-index: 1;
}
  
.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
  
.dropdown-content a:hover {
    background-color: #ddd;
}
  
.dropdown:hover .dropdown-content {
    display: block;
}
  
.dropdown:hover .dropdown-button {
    background-color: #0043d2;
}
