.banner {
    background-color: #0050F8;
    height: 70px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    color: white;
    box-shadow:rgba(0, 0, 0, 0.2) 0px 4px 8px;
}

.coast-companion {
    margin-left: 25px;
    font-size: x-large;
    font-weight: 700;
}

.login-form {
    text-align: center;
    background-color: #fafafa;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
}

.header {
    color: #0050F8;
    font-size: x-large;
    margin-bottom: 24px;
}

.login-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 500px;
    width: 400px;
    border-radius: 10px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 3px;
}

.login-fields {
    align-items: left;
}

.input-desc {
    font-size: medium;
    font-weight: 600;
    margin-top: 25px;
    margin-bottom: 5px;
    display: flex;
    align-self: left;
}

.username-desc {
    margin-top: 70px;
}

.username,
.password {
    height: 30px;
    width: 280px;
}

.submit-button {
    background-color: #0050F8;
    color: white;
    height: 30px;
    width: 180px;
    border-radius: 14px;
    border-width: 0;
    margin-top: 40px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px;
}

.submit-button:hover {
    background-color: #4e86ff;
}

.forgot-password {
    color: #0050F8;
    font-size: medium;
    font-weight: 600;
    margin-top: 10px;
}

.forgot-password:hover {
    color:#4e86ff;
}

.error-message {
    margin-top: 0;
    font-size: small;
    font-weight: 600;
    background-color: #ffefef;
    box-shadow: rgba(103, 0, 0, 0.2) 0px 2px 4px;;
    color: rgb(174, 2, 2);
    height: 20px;
    width: 100%;
}

.login-message {
    margin-top: 0;
    font-size: small;
    font-weight: 600;
    background-color: #ecf2ff;
    box-shadow: rgba(2, 0, 46, 0.2) 0px 2px 4px;;
    color: #0050F8;
    height: 20px;
    width: 100%;
}

.no-message {
    margin-top: 0;
    font-size: small;
    height: 20px;
}
