.maintain-kb-page {
    background-color: #fafafa;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
}

.kb-container {
    flex: 10 10 auto;
    overflow: auto;
    padding: 0 10%;
    margin-top: 100px;
}

.kb-header {
    display: flex;
    justify-content: space-between;
}

.knowledge-base-title {
    color:#0050F8;
}

.last-updated {
    margin-top: -10px;
    margin-bottom: 40px;
    font-weight: 600;
}

.search-by-source {
    font-weight: 600;
}

.kb-search {
    margin-bottom: 20px;
    font-weight: 600;
}

.add-data-button {
    background-color: #0050F8;
    color: white;
    border: none;
    height: 30px;
    width: 100px;
    border-radius: 20px;
    font-weight: bold;
    margin-top: 40px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px;
}

.maintain-kb-button {
    background-color: #0050F8;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 15px;
    cursor: pointer;
    width: 100%;
    max-width: 100px;
}

body.active-modal {
    overflow-y: hidden;
}

.modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}
.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #fafafa;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
    justify-content: center;
}

.modal-user-input-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 25px;
}

.file-name-input {
    height: 20px;
    margin-bottom: 10px;
}

.file-upload {
    margin-bottom: 40px;
}

.add-data-to-kb-button {
    background-color: #0050F8;
    color: white;
    border: none;
    height: 25px;
    width: 220px;
    border-radius: 20px;
    align-self: center;
}

.close-modal {
    position: absolute;
    width: 70px;
    top: 35px;
    right: 20px;
    padding: 5px 7px;
    background-color: #0050F8;
    color: white;
    border: none;
    border-radius: 20px;
}

.close-modal:hover, 
.add-data-to-kb-button:hover,
.maintain-kb-button:hover,
.add-data-button:hover {
    background-color: #4e86ff;
}

.clip-loader {
    display: flex;
    align-items: center;
    justify-content: center;
}

